.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 24px;
  top: 80px;
  left: 20px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  cursor: pointer;
  background-color: black;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 20px;
}

.toggle {
  position: absolute;
  top: -38px;
  left: 0px;
  font-size: 12px;
}

.slider:before {
  position: absolute;
  content: " ";
  width: 20px;
  height: 15px;
  left: 4px;
  bottom: 5px;
  background-color: white;
  border-radius: 20px;
}

input:checked + .slider {
  background-color: white;
}

.toggle1 {
  color: white;
}

input:focus + slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(15px);
  -ms-transform: translateX(15px);
  transform: translateX(15px);
  background-color: black;
  color: black;
}

input:checked + .toggle1 {
  color: red;
}

